.transition1 {
    margin: 10px;
    width:100px;
    height:100px; 
    transition: width 2s, height 2s;
    background-color: deeppink
  }
  .transition1:hover{
    width:200px;  
  }

  .transition2 {
    margin: 10px;
    width:100px;
    height:100px; 
    transition: width 2s, height 2s; transition-delay:2s;
    background-color: deeppink
  }
  .transition2:hover{
    width:200px;  
  }

  .transition3 {
    margin: 10px;
    width:100px;
    height:100px; 
    transition: width 2s, height 2s; transition-duration:2s;
    background-color: deeppink
  }
  .transition3:hover{
    width:200px;  
  }

  .transition4 {
    margin: 10px;
    width:100px;
    height:100px; 
    transition: width 2s, height 2s; transition-timing-function:ease-out;
    background-color: deeppink
  }
  .transition4:hover{
    width:200px;  
  }

  @keyframes example1 { from { background-color: deeppink; } to { background-color: deepskyblue; } }
.animation1 { width: 10%; height: 90%; background-color: deeppink; animation-name: example1; animation-duration: 4s; animation-iteration-count: 5} 

@keyframes example2 { 0% { background-color: deepskyblue; left: 0px; top: 0px; } 
            25% { background-color: yellow; left: 50px; top: 0px; } 
            50% { background-color: blue; left: 75px; top: 0px; } 
            100% { background-color: green; left: 0px; top: 0px; } } 

.animation2 { width: 100px; height: 100px; background-color: deeppink; 
    animation-name: example2; animation-duration: 8s; position: relative; 
    animation-delay: 2s; animation-iteration-count: 5; } 