.radio-toolbar {
  margin: 10px;
}

.radio-toolbar input[type="radio"] {
  opacity: 0;
  position: fixed;
  width: 0;
}

.radio-toolbar label {
    display: inline-block;
    background-color:#343a40;
    background-color:var(--main-ButtonBKColor-color);
    padding: 5px 25px;      
    font-size: 2.5vw;
    border: 2px solid #1697f9ff;
    border: 2px solid var(--main-ButtonColor-color);
    border-radius: 25px;
    color:#1697f9ff;
    color:var(--main-ButtonTextColor-color);
    margin:5px;
}


.radio-toolbar label:hover {
  background-color: deeppink;
}

.radio-toolbar input[type="radio"]:focus + label {
    border: 2px solid yellow;
}

.radio-toolbar input[type="radio"]:checked + label {
    background-color: aqua;
    border-color:#1697f9ff;
    border-color:var(--main-ButtonColor-color);
}


